.spin-wrapper {
  position: relative;
  margin-top: 32px;
  width: 100%;
  height: 150px;

  .spinner {
      position: absolute;
      height: 60px;
      width: 60px;
      border: 3px solid transparent;
      border-top-color: #F2644C;
      top: 50%;
      left: 50%;
      margin: -30px;
      border-radius: 50%;
      animation: spin 2s linear infinite;

      &:before, &:after {
          content:'';
          position: absolute;
          border: 3px solid transparent;
          border-radius: 50%;
      }
      
      &:before {
          border-top-color: #E0A4F4;
          top: -12px;
          left: -12px;
          right: -12px;
          bottom: -12px;
          animation: spin 3s linear infinite;
      }
      
      &:after {
          border-top-color: #7ACBF1;
          top: 6px;
          left: 6px;
          right: 6px;
          bottom: 6px;  
          animation: spin 4s linear infinite;
      }
  }
}

@keyframes spin {
  0% {transform: rotate(0deg);}
  100% {transform: rotate(360deg);}
}

.loading {
  font-family: Roboto;
}